* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background: linear-gradient(240.93deg, #ffddce 5.44%, #f8ddfd 95.58%);
}

.App {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
        Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    min-height: 100%;
    min-width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}

.burger {
    display: none;
}

.burger span {
    display: block;
    width: 25px;
    height: 4px;
    border-radius: 120px;
    background-color: rgb(8, 8, 8);
    margin: 5px;
}


nav {
    height: 120px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
   
}

.nav-links {
    display: flex;
    list-style: none;
    justify-content: space-around;
    align-items: center;
    width: 50%;
    min-height: 110px;
    margin-left: auto;
}

.nav-links li a {
    text-decoration: none;
    cursor: pointer;
    color: black;
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    display: block;
    padding: 0px;
    text-shadow: 2px 4px 4px rgba(0, 0, 0, 0.25);
}

.nav-links li a:hover {
    background: linear-gradient(240.93deg, #f77740 5.44%, #de6ef1 95.58%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 21px;
    text-align: center;
    align-items: center;
    transition: all 0.3s ease;
}

.logo {
    display: flex;
    position: absolute;
    left: 110px;
    top: 40px;
}

.logo a {
    cursor: pointer;
}
.eclips {
    z-index: -1;
    position: absolute;
    right: 0;
}

#home-page {
    height: 50vw;
    width: 100%;
    position: relative;
    overflow: hidden;
    flex-wrap: wrap;
}

#second-page {
    height: 40vw;
    background: linear-gradient(240.93deg, #fde0d48f 5.44%, #f8ddfd 95.58%);
    width: 100%;
    position: relative;
    flex-wrap: wrap;
}

.grid {
    display: grid;
    grid-template-columns: 50% 50%;
    width: 100%;
    min-height: 40vw;
    max-height: 100%;
}

.left-wrapper {
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.lines {
    width: 50%;
    left: 20%;
    position: absolute;
    height: 80%;
}

.lines span:nth-child(1) {
    width: 100%;
    height: 5px;
    background: white;
    position: absolute;
    animation: drop 0.5s ease;
}

.lines span:nth-child(2) {
    height: 100%;
    width: 5px;
    background: white;
    position: absolute;
    top: 0;
    right: 0;
    animation: drop2 0.7s ease;
}
.lines span:nth-child(3) {
    width: 100%;
    height: 5px;
    background: white;
    position: absolute;
    bottom: 0;
    right: 0;
    animation: drop4 1.2s ease;
}
.lines span:nth-child(4) {
    height: 100%;
    width: 5px;
    background: white;
    position: absolute;
    bottom: 0;
    left: 0;
    animation: drop3 1.3s ease;
}

.left-wrapper h1 {
    /* background: #f8ddf8; */
    background: linear-gradient(250.93deg, #fde0d400 0.44%, #f8ddf8 100.58%);

    position: absolute;
    left: 17%;
    top: 27%;
    padding: 3px;
}

.left-wrapper p {
    /* background: #f8ddf8;
     */
    background: linear-gradient(274.93deg, #fde0d400 0.44%, #f8ddf8 100.58%);
    position: absolute;
    width: 50%;
    left: 16.5%;
    top: 38%;
    word-spacing: 5px;
    letter-spacing: 2px;
    line-height: 2rem;
    padding: 3px;
}

.right-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.right-wrapper img {
    filter: drop-shadow(0px 0px 48px rgba(173, 0, 255, 0.57));
}
.about {
    display: grid;
    grid-template-columns: 50% 45% 5%;
    height: 100vh;
}
.about .left-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.description {
    width: 550px;
    position: relative;
    top: 15vw;
    left: 10%;
    align-items: center;
    font-family: "Montserrat", sans-serif;
    font-family: "Scope One", serif;
    justify-content: center;
}

.description h3,
h1 {
    text-align: center;
}

.description h1 {
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 50px;
    line-height: 49px;
    text-align: center;
    text-transform: capitalize;
    display: flex;
}

.description h3 {
    margin-top: 35px;
    color: #464444;
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 27px;
    line-height: 39px;
    text-align: center;
    text-transform: capitalize;
}

.contact-btn {
    position: relative;
    left: 31%;
    margin-top: 70px;
    border: none;
    width: 191px;
    height: 65px;

    background: linear-gradient(
        85.47deg,
        rgba(209, 131, 245, 0.65) -19.16%,
        #ffbc9f 123.53%
    );
    border-radius: 12px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 34px;
    line-height: 55px;
    text-align: center;
    filter: drop-shadow(0px 4px 7px rgba(0, 0, 0, 0.25));
    cursor: pointer;
}

.contact-btn:active {
    border: none;
    background: linear-gradient(
        85.47deg,
        rgba(201, 103, 247, 0.842) -19.16%,
        #f59870 123.53%
    );
}

.contact-btn:hover {
    filter: drop-shadow(0px 4px 7px rgb(255, 255, 255));
    transition: all 0.5s ease;
}

@keyframes drop {
    0% {
        opacity: 0;
        transform: translateX(-100px);
    }
    50% {
        opacity: 0.2;
    }
    80% {
        opacity: 0.3;
    }
    100% {
        opacity: 1;
        transform: translateX(0px);
    }
}
@keyframes drop2 {
    0% {
        opacity: 0;
        transform: translateY(-100px);
    }
    90% {
        opacity: 0.1;
    }
    95% {
        opacity: 0.3;
    }
    100% {
        opacity: 1;
        transform: translateX(0px);
    }
}
@keyframes drop3 {
    0% {
        opacity: 0;
        transform: translateY(100px);
    }
    85% {
        opacity: 0.2;
    }
    95% {
        opacity: 0.5;
    }
    100% {
        opacity: 1;
        transform: translateX(0px);
    }
}
@keyframes drop4 {
    0% {
        opacity: 0;
        transform: translateX(100px);
    }

    100% {
        opacity: 1;
        transform: translateX(0px);
    }
}

@keyframes drop-text {
    0% {
        opacity: 0;
        transform: translateX(-500px);
    }
    85% {
        opacity: 0.2;
    }

    100% {
        opacity: 1;
        transform: translateX(0px);
    }
}

@media only screen and (max-width: 1200px) {
    #home-page {
        min-height: 70vw;
    }

    #second-page {
        min-height: 70vh;
    }
    .grid {
        height: 100%;
    }

    .lines {
        width: 80%;
    }

    .left-wrapper p {
        width: 75%;
        line-height: 1.5rem;
        margin-top: 20px;
    }

    .left-wrapper h1 {
        top: 25%;
    }

    .right-wrapper img {
        width: 90%;
    }

    .nav-links {
        margin-right: 50px;
    }
}

@media only screen and (max-width: 768px) {
    #home-page {
        min-height: 100vh;
        max-width: 768px;
    }

    #second-page {
        min-height: 90vh;
        max-width: 768px;
    }
    .eclips {
        width: 50vw;
        min-height: 10vw;
    }

    .description {
        max-width: 250px;
        left: 15%;
    }

    .contact-btn {
        margin-top: 20px;
        border: none;
        width: 130px;
        height: 55px;
        position: relative;
        background: linear-gradient(
            85.47deg,
            rgba(209, 131, 245, 0.65) -19.16%,
            #ffbc9f 123.53%
        );
        border-radius: 12px;
        font-family: Montserrat;
        font-style: normal;
        font-weight: normal;
        font-size: 25px;
        line-height: 55px;
        text-align: center;
        filter: drop-shadow(0px 4px 7px rgba(0, 0, 0, 0.25));
    }

    .grid {
        min-height: 100%;
        width: 100%;
        flex-wrap: wrap;
    }

    .lines {
        min-width: 70%;
        min-height: 30vw;
    }

    .left-wrapper p {
        width: 80%;
        line-height: 1.2rem;
        margin-top: 20px;
    }

    .left-wrapper h1 {
        top: 20%;
    }

    .nav-links {
        position: absolute;
        right: -10%;
        height: 70vh;
        top: 10vh;
        background: linear-gradient(
            240.93deg,
            #f77740c4 5.44%,
            #dd6ef15e 95.58%
        );
        border-radius: 50px 0 0 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 50%;
        z-index: 1;
        transform: translateX(100%);
        transition: transform 1s ease;
    }
    .burger {
        position: absolute;
        display: block;
        cursor: pointer;
        right: 25px;
        margin: 15px;
    }
}

.nav-active {
    transform: translateX(0%);
}





@media only screen and (max-width: 375px) {
    #home-page {
        min-height: 100vh;
        max-width: 375px;
    }

    #second-page {
        height: 100vh;
        max-width: 375px;
    }
    .eclips {
        width: 50vw;
        min-height: 10vw;
    }

    .description {
        max-width: 250px;
        left: 11%;
    }

    .description h3 {
        position: relative;
        display: flex;
        left: 7%;
    }

    .contact-btn {
        border: none;
        width: 130px;
        height: 55px;

        background: linear-gradient(
            85.47deg,
            rgba(209, 131, 245, 0.65) -19.16%,
            #ffbc9f 123.53%
        );
        border-radius: 12px;
        font-family: Montserrat;
        font-style: normal;
        font-weight: normal;
        font-size: 25px;
        line-height: 55px;
        text-align: center;
        filter: drop-shadow(0px 4px 7px rgba(0, 0, 0, 0.25));
    }

    .grid {
        height: 100%;
        flex-wrap: wrap;
        grid-template-columns: none;
        grid-template-rows: 50% 50%;
    }

    .lines {
        min-width: 85%;
        min-height: 90%;
    }
    .left-wrapper {
        right: 8%;
    }

    .left-wrapper p {
        width: 80%;
        line-height: 1.2rem;
        margin-top: 20px;
    }

    .left-wrapper h1 {
        top: 20%;
    }

    body {
        overflow-x: hidden;
    }

    .footer-container {
        width: 375px;
    }

    .nav-links {
        right: -14%;
    }

    .footer-left-wrapper *{
     font-size: 0.9rem;
     letter-spacing: -1px;
    }

   
}






@media only screen and (max-width: 360px) {
    #home-page {
        min-height: 120vh;
        max-width: 360px;
    }

 
    #second-page {
        height: 130vh;
        max-width: 360px;
    }
    .eclips {
        width: 50vw;
        min-height: 10vw;
    }

    .description {
        max-width: 250px;
        left: 11%;
    }

    .description h3 {
        position: relative;
        display: flex;
        left: 7%;
    }

    .contact-btn {
        border: none;
        width: 130px;
        height: 55px;

        background: linear-gradient(
            85.47deg,
            rgba(209, 131, 245, 0.65) -19.16%,
            #ffbc9f 123.53%
        );
        border-radius: 12px;
        font-family: Montserrat;
        font-style: normal;
        font-weight: normal;
        font-size: 25px;
        line-height: 55px;
        text-align: center;
        filter: drop-shadow(0px 4px 7px rgba(0, 0, 0, 0.25));
