    .grid {
        height: 100%;
        flex-wrap: wrap;
        grid-template-columns: none;
        grid-template-rows: 50% 50%;
        margin: 25px 0px;
    }

    .lines {
        min-width: 85%;
        min-height: 95%;
    }
    .left-wrapper {
        right: 8%;
        
    }

    .left-wrapper p {
        width: 80%;
        line-height: 1.2rem;
        margin-top: 20px;
    }

    .right-wrapper img{
        margin: 15px 0;
    }

    .left-wrapper h1 {
        top: 20%;
    }

    body {
        overflow-x: hidden;
    }

    .footer-container {
        width: 360px;
    }

    .nav-links {
        right: -14%;
        top: 20vh;
    }

    .footer-left-wrapper *{
     font-size: 0.9rem;
     letter-spacing: -1px;
    }

    
    .burger{
        right: 10px;
    }
}




.footer-container {
    height: 40vh;
    background: rgba(241, 222, 250, 0.801);
    display: grid;
    grid-template-columns: 50% 50%;
    position: relative;
}

.footer-left-wrapper {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    padding: 70px;
    position: relative;
}

.footer-middle-line {
    background: rgba(0, 0, 0, 0.329);
    width: 1px;
    height: 25vh;
    right: 0;
    position: absolute;
}

.footer-left-wrapper .phoneNo {
    display: flex;
    font-family: "Poppins";
    padding: 5px;
    letter-spacing: 0.15rem;
}
.footer-left-wrapper .phoneNo h4 {
    margin-right: 10px;
}

.footer-left-wrapper .email {
    padding: 5px;
    display: flex;
    font-family: "Poppins";
    letter-spacing: 0.15rem;
}
.footer-left-wrapper .email h4 {
    margin-right: 10px;
}

.footer-right-wrapper {
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 70px;
    align-items: center;
}

.footer-right-wrapper button {
    border: 1px solid rgba(0, 0, 0, 0.582);
    width: 15vh;
    height: 7vh;
    background: rgba(255, 255, 255, 0.986);
    border-radius: 100px;
    text-align: center;
    filter: drop-shadow(0px 4px 7px rgba(0, 0, 0, 0.25));
    cursor: pointer;
    font-family: "Poppins";
    font-size: 1.5rem;
    letter-spacing: 2px;
}
.footer-right-wrapper button:hover {
    background: linear-gradient(
        85.47deg,
        rgba(209, 131, 245, 0.65) -19.16%,
        #ffbc9f 123.53%
    );

    transform: translateY(-10px);
    transition: transform 1s ease;
    font-size: 1.6rem;
    filter: drop-shadow(0px 0px 7px rgb(231, 90, 250));
}
